.catalog-filter {
    flex-shrink: 0;
    width: 250px;
    margin-right: 30px;

    @include sm-block() {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: 1000;
        width: 100%;
        max-width: 355px;
        overflow: hidden;
        transform: translateX(-100vw);
        transition: $trs;
        background-color: #fff;
        will-change: transform;

        // .btn {
        //     width: 100%;
        //     padding: 8px 20px;

        //     & + .btn {
        //         margin-left: 15px;
        //     }
        // }

        &.is-open {
            transform: translateX(0);
        }
    }

    @include xs-block() {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        z-index: z(modal) + 2;
        width: 100%;
        max-width: 100%;
        height: 100%;
        // max-height: calc(100vh - 50px);
        margin-right: 0;
        overflow: hidden;
        transform: translateY(100vh);
        transition: $trs;
        border: 0;
        border-radius: 0;
        background-color: #fff;
        will-change: transform;
        @include flexColumn;

        &.is-open {
            transform: translateY(0);
        }
    }

    &__toggle {
        margin-bottom: 15px;
        @include xs-block() {
            height: 60px;
            margin: 0;
            border: 1px solid transparent;
            @include tr;
        }

        .bb-checkbox--radio_inline {
            position: relative;
            @include xs-block() {
                width: 44px;
                height: 44px;
                padding: 0;

                &:first-child {
                    padding-left: 0;
                }

                &:last-child {
                    padding-left: 0;
                }

                .bb-checkbox__title {
                    display: none;
                }
            }

            &::before {
                position: absolute;
                top: 5px;
                left: 25px;
                font-size: 16px;
                @include fa('solid');
                @include xs-block() {
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }

            &:first-child {
                padding-left: 20px;

                &::before {
                    content: '\f0c9';
                }
            }

            &:last-child {
                padding-left: 5px;

                &::before {
                    content: '\f041';
                }
            }
        }
    } //.catalog-filter__toggle

    &__inner {
        @include box;

        @include sm-block() {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            height: 100%;
            border: 0;
            box-shadow: none;
        }
    } //.catalog-filter__inner

    &__header {
        position: relative;
        display: flex;
        align-items: center;
        height: 50px;
        padding: 0 15px;
        border-bottom: 1px solid $color-border--lightest;

        @include sm-block() {
            flex-shrink: 0;
            width: 100%;
            height: 50px;
            padding: 0 10px;
        }

        .catalog-filter {
            &__title {
                position: relative;
                flex: 1;
                width: auto;
                margin: 0;

                font-size: 18px;
                line-height: 1.2;
                @include sm-block() {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }

                span {
                    display: none;
                    color: $color-text;
                    font-size: 12px;
                    font-weight: 300;
                    @include sm-block() {
                        display: inline-block;
                    }
                }
            }
        }

        .button-link {
            margin-left: auto;
            padding: 0;
            font-size: 13px;
            font-weight: 300;

            @include sm-block() {
                position: absolute;
                top: 0;
                right: 0;
                width: 50px;
                height: 50px;

                span {
                    display: none;
                }
            }

            .catalog-filter__btn_close {
                margin-right: 0;

                @include min-screen(769) {
                    display: none;
                }
            } //.catalog-filter__header .button-link .catalog-filter__btn_close
        } //.catalog-filter__header .button-link
    } //.catalog-filter__header

    &__body {
        @include sm-block() {
            flex-grow: 1;
            width: 100%;
            @include scroll;
        }

        // @include xs-block() {
        //     max-height: calc(100vh - 50px);
        // }
    } //.catalog-filter__body

    &__footer {
        display: none;
        flex-shrink: 0;
        height: auto;
        padding: 15px;
        border-top: 1px solid $color-border--lightest;

        @include sm-block() {
            display: block;
            // width: 100%;
            padding: 10px;
            background-color: #fff;
            // @include flexCenter;
        }

        .btn {
            flex-basis: 50%;
        }

        .btn-link {
            flex-basis: 50%;
            // justify-content: center;
            // height: 42px;
            // line-height: 42px;
            // text-align: center;
        }
    } //.catalog-filter__footer

    &__item {
        display: flex;
        flex-wrap: wrap;
        padding: 15px;
        border-bottom: 1px solid $color-border--lightest;

        @include sm-block() {
            margin: 0;
            box-shadow: none;
        }

        &:last-child {
            border-bottom: none;
        }

        &_head {
            @include flexCenter;

            .catalog-filter {
                &__title {
                    width: auto;
                    margin: 0;

                    font-size: 18px;
                }
            }

            .button-link {
                margin-left: auto;
                font-size: 13px;
                font-weight: 300;
            }
        } //.catalog-filter__item_head

        &_service {
            .bb-checkbox {
                margin: auto auto 5px;
            } //.catalog-filter__item_service .bb-checkbox
        } //.catalog-filter__item_service

        &_place,
        &_gender {
            .bb-checkbox {
                width: 100%;
                margin-bottom: 5px;
            }
        } //.catalog-filter__item_place

        &_metro,
        &_area {
            padding-bottom: 20px;
        } //.catalog-filter__item_area

        &_buttons {
            .button-animate {
                padding: 8px 45px;
                border: 1px solid $color-border;
            }
        } //.catalog-filter__item_buttons

        & + .catalog-filter__item {
            margin-top: 0;
        }

        .bb-checkbox {
            @include text-overflow;

            @include sm-block() {
                margin-bottom: 8px;
            }

            &:checked ~ .bb-checkbox__title {
                border-width: 1px;
            }

            &__title {
                display: inline-flex;
                color: $gray--darkest;
            } //.catalog-filter__item .bb-checkbox__title

            &--transform {
                @include sm-block() {
                    display: inline-block;
                    width: auto;
                    margin: 0 10px 6px 0;
                    padding: 5px 0;

                    input:checked ~ .bb-checkbox__title {
                        border: 1px solid $gray;
                    }
                }
            } //.catalog-filter__item .bb-checkbox--transform
        } //.catalog-filter__item .bb-checkbox
    } //.catalog-filter__item

    &__title {
        width: 100%;
        margin: 0 0 15px;
        color: $color-text--darkest;
        font-size: 15px;
        font-weight: 600;

        @include xs-block() {
            font-size: 16px;
        }

        &:hover {
            @include max(sm) {
                box-shadow: none;
            }
        }
    } //.catalog-filter__title

    &__content {
        width: 100%;
        @include flexColumn;

        @include sm-block() {
            display: block;
        }

        .bb-select {
            &::before {
                right: 10px;
            }
        }
    } //.catalog-filter__content

    &__range {
        width: 100%;
        margin-top: 20px;
        margin-bottom: 10px;
        padding: 0 13px;
    } //.catalog-filter__range

    &__slider {
        margin-bottom: 15px;
    } //.catalog-filter__slider

    &__category {
        width: 100%;
    } //.catalog-filter__category

    &__btn {
        width: 50%;
        height: 40px;
        border: none;
        outline: none;
        background-color: transparent;
        font-size: 14px;
        font-weight: 300;
        line-height: 40px;
        text-transform: uppercase;

        &_reset {
            display: block;
            border-top: 1px solid $color-border--lightest;
            text-align: center;
        } //.catalog-filter__btn_close

        &_apply {
            border-top: 1px solid $color-accent;
            background-color: $color-accent;
            color: #fff;
        } //.catalog-filter__btn_apply

        &_close {
            background-color: transparent;
            fill: $color-text;
        } //.catalog-filter__btn_close

        &_filter {
            position: fixed;
            right: 10px;
            bottom: 10px;
            z-index: 90;
            display: none;
            width: 55px;
            height: 55px;
            border: 0;
            border-radius: 50%;
            background-color: $color-accent;
            box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
                0 2px 10px 0 rgba(0, 0, 0, 0.12);
            @include sm-block() {
                display: flex;
            }

            &:focus {
                border: none;
                outline: none;
            }

            .icon {
                width: 19px;
                height: 19px;
                margin: auto;
                fill: #fff;
            }
        } //.catalog-filter__btn_filter
    } //.catalog-filter__btn

    .category {
        &__dropdown {
            margin-top: 10px;
        }
    }
} //.catalog-filter
