.page-home {
    .header {
        z-index: 100;
        background: transparent;

        &__top {
            background-color: #fff;
        }

        &__inner {
            border-bottom: 0;
        }

        &__bottom {
            .nav {
                &__item {
                    &:hover {
                        .nav__link {
                            background-color: rgba(#000, 0.55);
                        }
                    }
                }

                &__list {
                    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
                }

                &__link {
                    color: #fff;
                }
            }
        }
    }

    .header-action {
        margin-left: auto;

        &__search {
            display: none;
        }
    }

    .nav-dropdown {
        box-shadow: 3px 20px 20px rgba(0, 0, 0, 0.4);
    }

    .search__group--services {
        display: none;
    }

    .button-icon--info {
        &::before {
            color: $color-text;
        }
    }

    .firstscreen {
        display: flex;
        height: 550px;
        max-height: 80vh;

        @include sm-block() {
            height: auto;
            max-height: none;
            padding-bottom: 0;
        }

        &__title {
            margin: 65px 0 15px;
            @include xs-block() {
                line-height: 1.4;
                text-transform: uppercase;
            }
        }

        &__inner {
            padding: 70px 0;

            @include sm-block() {
                padding: 0;
            }

            &::before {
                display: none;
            }
        }
    }
} //.page-home

.offer {
    width: 100%;
    max-width: 360px;
    padding: 30px 30px 25px;
    border-radius: $border-radius;
    background-color: rgba(255, 255, 255, 0.8);

    @include md-block() {
        margin-right: 40px;
        margin-left: 10px;
    }

    @include sm-block() {
        margin-right: 0;
        margin-left: 0;
    }

    @include xs-block() {
        max-width: 100%;
        padding: 0 10px;
    }

    &__title {
        display: block;
        margin-bottom: 20px;
        color: $color-text--darkest;
        font-size: 30px;
        font-weight: 600;
        line-height: 1.2;

        @include xs-block() {
            font-size: 26px;
            font-weight: 500;
            text-align: center;
        }
    } //.offer__title

    &__desc {
        display: block;
        margin-bottom: 25px;
        color: $color-text;

        font-size: 17px;

        @include xs-block() {
            text-align: center;
        }
    } //.offer__desc

    &__note {
        display: block;
        width: 100%;
        margin-top: 5px;
        color: $color-text--light;
        font-size: 14px;
        text-align: center;
    } //.offer__note

    &__btn {
        width: 100%;
        @include xs-block() {
            width: 80vw;
        }
    } //.offer__btn
} //.offer

.bb-category {
    padding: 0;

    &__items {
        @include flexColumn;
    } //.category__items

    &__item {
        position: relative;
        height: 200px;
        margin-bottom: 1px;
        @include cover;

        @include xs-block() {
            margin-right: 0;
            margin-bottom: 1px;
            border-radius: 0;
        }

        &:nth-child(even) {
            background-color: $color-main;

            .bb-category__title {
                left: 10%;
            }
        }

        &:nth-child(odd) {
            background-color: $color-primary--light;

            .bb-category__title {
                right: 10%;
            }
        }
    } //.category__item

    &__title {
        color: #fff;
        font-family: roboto-600;
        font-size: 17px;
        letter-spacing: 0.5px;
        text-transform: uppercase;
        @include centre('y');
    } //.category__title
} //.bb-category

.about {
    .text-block {
        margin-bottom: 0;

        &__item:last-child {
            @include xs-block() {
                margin-bottom: 0;
            }
        }
    }
} //.about

.description {
    &__inner {
        display: flex;
        max-width: 950px;
        margin: auto;

        @include xs-block() {
            flex-direction: column;
            padding: 0 10px;
            text-align: center;
        }
    } //.desc__inner

    &__title {
        margin-bottom: 30px;
        color: #000;
        font-size: 25px;
        font-weight: 600;

        @include xs-block() {
            line-height: 1.2;
        }
    } //.desc__title

    &__left {
        justify-content: center;
        margin-right: 100px;
        @include flexColumn;

        @include xs-block() {
            margin-right: 0;
            margin-bottom: 50px;
        }
    } //.desc__left

    &__right {
        img {
            @include xs-block() {
                width: 60%;
                margin: auto;
            }
        }
    } //.desc__right
} //.description

.blog {
    margin-bottom: 0;

    @include xs-block() {
        margin: 0;
        padding: 0;
    }

    &__inner {
        @include row-flex();
        @include md(justify-content, center);
    } //.blog__inner

    &__post {
        margin-bottom: $gutter * 2;
        @include col();
        @include size(3);
        @include size-sm(6);
        @include size-i5(12);
    } //.blog__post

    &--manicure {
        background: url('../img/general/bg-pattern-blue.svg'), #f3f6ff;
        background-size: 125px;
    }

    .bb-title {
        @include xs-block() {
            display: none;
        }
    }
} //.blog

.blog-item {
    z-index: 2;
    align-items: center;
    justify-content: space-around;
    max-width: 300px;
    height: 300px;
    margin: 0 auto 30px;
    text-align: center;
    @include cover;
    @include sectionOverlay;
    @include flexColumn;
    @include col();
    @include size(4);
    @include size-sm(6);

    @include xs-block() {
        width: 100%;
        max-width: 100%;
        height: 261px;
        margin: 0 0 1px;
    }

    &:hover {
        &::before {
            background-color: rgba(#000, 0.65);
        }

        .blog-item {
            &__btn {
                visibility: visible;
                opacity: 1;
                transform: translateY(-10px);
            }

            &__title {
                transform: translateY(40px);
            }
        }
    }

    &__wrap {
        max-width: 960px;
        margin: auto;
        @include row-flex();
        @include max(sm) {
            justify-content: center;
        }
    } //.blog-item__wrap

    &__title {
        transform: translateY(70px);
        color: #fff;
        font-size: 24px;
        font-weight: 300;
        @include tr;
        @include xs-block() {
            transform: translateY(0);
        }

        span {
            display: block;
            margin-top: 5px;
            font-size: 12px;
            font-weight: 600;
            text-transform: uppercase;
        }
    } //.blog-item__title

    &__btn {
        visibility: hidden;
        opacity: 0;
        transform: translateY(-30px);
        border-width: 1px !important;
        font-weight: 300;
        @include xs-block() {
            display: none;
        }
    } //.blog-item__btn
} //.blog-item

.service-category {
    justify-content: center;
    padding: 0 10px;
    @include row-flex;

    @include xs-block() {
        flex-wrap: wrap;
        padding: 0;
    }

    &__item {
        position: relative;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
        overflow: hidden;
        cursor: pointer;
        @include col;
        @include size(2.4);
        @include size-sm(4);
        @include size-xs(12);

        @include xs-block() {
            flex-shrink: 0;
            margin-right: 10px;
            margin-bottom: 2px;
            margin-left: 10px;
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(47, 47, 47, 0.5);
        }

        &:hover {
            .service-category__img {
                transform: scale(1.1);
            }
        }
    } // .service-category__item

    &__item--no-color {
        position: relative;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
        overflow: hidden;
        @include col;
        @include size(2.4);
        @include size-sm(4);
        @include size-xs(12);

        @include xs-block() {
            flex-shrink: 0;
            margin-right: 10px;
            margin-bottom: 2px;
            margin-left: 10px;
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
        }

        &:hover {
            .service-category__img {
                transform: scale(1.1);
            }
        }
    }

    &__title {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
        transform: translate(-50%, -50%);
        color: #fff;
        font-weight: 500;
        text-align: center;
        text-transform: uppercase;
    } // .service-category__title

    &__img {
        flex-shrink: 0;
        width: auto;
        min-width: 100%;
        height: 100%;
        min-height: 100%;
        @include tr;
    } // .service-category__img
} // .service-category

.section {
    .scroll {
        overflow: hidden;
    }
}
