    .catalog-content {
        @include landscape() {
            width: 100%;
        }

        &__sorting {
            margin-right: 0;
            margin-left: auto;

            @include xs-block() {
                margin-left: 0;

                select {
                    padding-left: 0;
                }
            }

            .select2-container
                .select2-selection--single
                .select2-selection__rendered {
                font-size: 14px;
            }
        } //.page-market .page-market .catalog-content__sorting

        &__top {
            padding: 5px 0;
            border-radius: 0;
            box-shadow: none;
            @include border(bottom);

            @include xs-block() {
                flex-direction: column;
                align-items: flex-start;
                padding: 0 0 5px;
            }
        } //.page-market .page-market .catalog-content__top

        &__view-toggle {
            border-radius: 0;

            &.view-toggle {
                margin-left: auto;
                padding-right: 10px;
                background-color: #fff;
                box-shadow: none;
                @include xs-block() {
                    padding-right: 0;
                }

                .view-toggle {
                    &__item {
                        width: auto;
                        height: auto;
                        box-shadow: none;

                        &:last-child {
                            margin-left: 15px;
                            border: 0;
                            background-color: #fff;
                        }
                    }

                    &__text {
                        margin-left: 5px;
                    }
                }
            }
        } //.page-market .page-market .catalog-content__view-toggle

        .bb-input {
            &__title {
                margin-top: 5px;
                @include max(xs) {
                    display: none;
                }
            }

            .select2-dropdown {
                @include border(top);

                &::before,
                &::after {
                    content: '';
                    position: absolute;
                    right: 7px;
                    width: 0;
                    height: 0;
                    border: solid transparent;
                    pointer-events: none;
                }

                &::before {
                    top: -12px;
                    margin: 0 -1px;
                    border-width: 6px;
                    border-bottom-color: $color-border--lighten;
                }

                &::after {
                    bottom: 100%;
                    border-width: 5px;
                    border-bottom-color: #fff;
                }
            }
        } //.page-market .page-market .catalog-content .bb-input
    } //.page-market .page-market .catalog-content
} //.page-market

.market {
    // padding-top: 100px;
    background-color: #fff;

    // @include sm-block() {
    //     padding-top: 60px;
    // }

    // @include xs-block() {
    //     padding-top: 50px;
    // }

    @include landscape() {
        padding-top: 0;
    }

    &__logo {
        width: 170px;
    } //.market__logo

    &.has-offset-top {
        margin-top: 100px;

        @include sm-block() {
            margin-top: 60px;
            padding-top: 0;
        }
        @include xs-block() {
            margin-top: 50px;
            padding-top: 0;
        }

        &--small {
            margin-top: 60px;

            @include sm-block() {
                margin-top: 60px;
                padding-top: 0;
            }
            @include xs-block() {
                margin-top: 50px;
                padding-top: 0;
            }
        }
    } //.market.has-offset-top

    // .mr-except-mobile {
    //     margin-right: 60px;

    //     @include md-block() {
    //         margin-right: 10px;
    //     }
    // } //.market .mr-except-mobile

    &.order {
        .order__inner {
            @include sm-block() {
                margin-top: 10px;
                padding-top: 0;
            }
        }
    }
} //.market

.market-section {
    margin-bottom: $section-indent;
} //.market-section

.market-category {
    &__list {
        @include row-flex;
    } //.market-category__list

    &__item {
        position: relative;
        display: flex;
        min-height: 250px;
        margin-bottom: 10px;
        overflow: hidden;
        border-radius: $border-radius;
        @include col;
        @include size(6);
        @include size-xs(12);

        @include xs-block() {
            align-items: center;
            min-height: auto;
        }

        &:hover {
            img {
                transform: scale(1.03);
            }
        }

        img {
            @include tr;
        } //.market-category__item img
    } //.market-category__item

    &__title {
        margin-bottom: 10px;
        color: $gray--dark;
        font-size: 20px;
        font-weight: 500;
        vertical-align: bottom;
        @include xs-block() {
            margin-bottom: 0;
            font-size: 18px;
        }
    } //.market-category__title

    &__text {
        margin-bottom: 15px;
        color: $gray--dark;

        font-size: 14px;
        font-weight: 400;

        @include xs-block() {
            display: none;
        }
    } //.market-category__text

    &__desc {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        order: 1;
        width: 70%;
        padding: 15px 30px;
        border-right: 0;
        @include md-block() {
            border-left: 0;
        }
        @include xs-block() {
            order: 2;
            padding: 10px;
            border: 0;
        }
    } //.market-category__desc

    &__btn-imit {
        padding: 5px 20px;
        border-radius: $border-radius;
        color: $gray--dark;
        font-size: 14px;
        @include border(all);

        @include xs-block() {
            display: none;
        }

        &:hover {
            background-color: #fff;
            cursor: pointer;
        }
    } //.market-category__btn-imit

    &__arrow {
        display: none;

        @include xs-block() {
            position: absolute;
            top: 50%;
            right: 5px;
            display: block;
            transform: translateY(-50%);
        }
    } //.market-category__arrow
} //.market-category

.advise {
    width: 100%;

    &__title {
        margin-bottom: 30px;
        color: $gray--darken;
        font-size: 26px;
        font-weight: 500;
        text-align: center;
    } //.advise__title

    &__content {
        margin-top: 20px;
    } //.advise__content
} //.advise

.changer {
    width: 100%;
    padding-bottom: 0;
    overflow-y: hidden;
    background-color: #fff;

    &__title {
        display: block;
        margin-bottom: 5px;
        font-size: 14px;
        word-spacing: nowrap;
        @include nosel;
    } //.changer__title

    &__list {
        flex-wrap: wrap;
        flex-wrap: wrap;
        width: 100%;
        @include flexCenter;
    } //.changer__list

    &__item {
        position: relative;
        margin-right: 15px;
        margin-bottom: 15px;

        @include xs-block() {
            margin-right: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &.is-checked {
            .changer {
                &__link {
                    background-color: $color-accent;
                    color: #fff;
                    @include border(all, $color-accent);

                    &:empty {
                        color: red;
                    }
                } //.changer__item.is-checked .changer__link

                &__reset {
                    position: static;
                    z-index: z(fixed);
                    visibility: visible;
                    opacity: 1;
                    padding: 10px 0 10px 10px;
                } //.changer__item.is-checked .changer__reset
            }
        } //.changer__item.is-checked
    } //.changer__item

    &__link {
        min-height: 33px;
        padding: 0 13px;
        transition: initial;
        border: 1px solid transparent;
        border-radius: $border-radius;
        color: $gray;
        font-size: 14px;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        @include flexCenter;

        &:hover {
            border-color: $color-border--lightest;
            color: initial;

            @include xs-block() {
                border: 0;
            }
        }
    } //.changer__link

    &__reset {
        position: absolute;
        // top: 0;
        // right: 0;
        z-index: -1;
        visibility: hidden;
        opacity: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        cursor: pointer;

        .icon {
            width: 10px;
            height: 10px;
            margin: auto;
            fill: #fff;
        }
    } //.changer__reset

    &__counter {
        margin-left: 5px;
        color: $gray;
        font-size: 13px;
    } //.changer__counter

    &-bg {
        &--dark {
            .changer__item {
                &.is-checked {
                    .changer__link {
                        border-color: $gray;
                        background-color: $gray;
                    }
                }
            }
        } //.changer-bg--dark
    } //.changer-bg

    &--outline {
        .changer {
            &__link {
                background-color: transparent;
                @include border(all);
            } //.changer--outline .changer__link
        }
    } //.changer--outline

    &--small-offset {
        .changer {
            &__item {
                margin-right: 10px;
                margin-bottom: 10px;

                &:last-child {
                    margin-right: 0;
                }
            } //.changer--small-offset .changer__item
        }
    } //.changer--small-offset
} //.changer

.point-delivery {
    max-height: 40vh;
    overflow-y: auto;

    &__title,
    &__text-light {
        color: $gray--light;
        font-size: 12px;
    } //.point-delivery__title

    &__text {
        font-weight: 500;
    } //.point-delivery__text

    &__desc {
        display: block;
        margin-bottom: 20px;
        font-size: 14px;
    } //.point-delivery__desc

    &__list {
        &-item {
            &::before {
                content: '—';
                color: $color-success;
                font-size: 18px;
                font-weight: 500;
            }
        }
    }

    &__address {
        margin-bottom: 5px;
        font-size: 18px;
        font-weight: 500;
    } //.point-delivery__address

    &__cost {
        font-size: 14px;
    } //.point-delivery__cost

    &__btn {
        min-height: 28px;
        padding: 0 20px;
        font-size: 14px;
        line-height: 27px;
    } //.point-delivery__btn

    &__btn-box {
        justify-content: flex-start;

        .point-delivery__btn {
            padding: 0 20px;
        } //.point-delivery__btn-box .point-delivery__btn
    } //.point-delivery__btn-box

    &__item {
        padding-top: 10px;

        & + .point-delivery__item {
            margin-top: 15px;
            @include border(top);
        }
    } //.point-delivery__item

    a.point-delivery__desc {
        color: $color-info;

        &:hover {
            color: $color-info--light;
        }
    } //a.point-delivery__desc

    & > * + *,
    &__item > * + * {
        margin-top: 10px;
    }
} //.point-delivery

#delivery-map {
    .point-delivery {
        &__address {
            max-width: 30ch;
            color: $color-text--darkest;
            font-size: 15px;
            font-weight: 600;
        }
    }
}

.modal {
    &.modal--fluid {
        .modal-title {
            font-size: 18px;
            font-weight: 500;
            text-align: center;
        } //.modal--fluid .modal-title
    } //.modal--fluid

    &-header {
        @include xs-block() {
            padding: 0 15px;
        }
    } //.modal-header

    &-body {
        .point-delivery {
            padding: 20px;

            &__btn {
                min-height: 40px;
            } //.modal-footer .point-delivery__btn

            &__text-light {
                margin-bottom: 20px;
            }
        } //.modal-body .point-delivery
    } //.modal-body

    &-footer {
        .point-delivery__btn {
            min-height: 40px;
        } //.modal-footer .point-delivery__btn
    } //.modal-footer
} //.modal
