.page-catalog {
    .wrapper {
        &__inner {
            padding-top: 100px;

            @include sm-block() {
                margin-top: 60px;
                padding-top: 0;
            }

            @include xs-block() {
                // margin-top: 50px;
                margin-top: 0;
                // padding-top: 50px;
            }
        }
    }

    .catalog {
        @include xs-block() {
            margin-top: 50px;
        }
    }

    .page-title {
        @include xs-block() {
            // margin: 0 auto 15px;
            // font-weight: 500;
            // font-weight: 500;
            // font-size: 32px;
            // text-align: center;
            // @include textShadow;
            margin: 0;
            font-size: 22px;

            span {
                // font-size: 32px;
                font-size: 22px;
                // display: block;
                font-weight: 500;
                font-weight: 500;
            }
        }

        &__wrap {
            @include xs-block() {
                position: relative;
                flex-direction: column;
                align-items: flex-start;
                margin: 20px 0 25px;
            }
        } //.page-catalog .page-title__wrap
    } //.page-catalog .page-title

    .page-subtitle {
        @include xs-block() {
            font-size: 14px;
            font-weight: 300;
            // @include textShadow;

            strong {
                font-size: 15px;
            }
        }
    } //.page-catalog .page-subtitle
} //.page-catalog

.catalog {
    &__inner {
        align-items: flex-start;
        margin-bottom: $section-indent;
        @include flex;
        @include xs-block() {
            flex-direction: column;
            flex-wrap: wrap;
        }
    } //.catalog__inner

    &__title {
        font-size: 28px;
        text-align: center;

        @include xs-block() {
            margin: 0 0 20px;
        }
    } //.catalog__title

    &__top-line {
        flex-shrink: 0;
        margin-left: auto;
        @include flexCenter;

        @include xs-block() {
            margin: 0;
        }
    } //.catalog__top-line

    &__view-toggle {
        margin-left: 30px;
        @include xs-block() {
            width: 100%;
            margin: 0 0 15px auto;

            .view-toggle__item {
                width: 50%;
            }
        }
    } //.catalog__view-toggle

    &__seoblock {
        margin-left: 280px;

        @include max(sm) {
            margin-left: 0;
        }
    } //.catalog__seoblock

    &__promo-block {
        margin-bottom: 15px;
    } //.catalog__promo-block

    &__pagination {
        margin-top: $gutter * 2.5;
        // margin-left: 280px;

        @include max(sm) {
            margin-left: 0;
        }
    } //.catalog__pagination
} //.catalog

.catalog-content {
    position: relative;
    width: calc(100% - 250px - 30px);
    margin-left: auto;

    @include sm-block() {
        width: 100%;
    }

    &__top {
        min-height: 48px;
        margin-bottom: 15px;
        padding: 5px 20px;

        @include flexCenter;
        @include box;
        @include tr;

        @include xs-block() {
            padding: 5px 10px;
        }

        &.no-border {
            border-radius: 0 0 $border-radius $border-radius;
        }
    } //.catalog-content__top

    &__result {
        font-size: 14px;
        white-space: nowrap;

        strong {
            color: #000;
            font-size: 15px;
        }
    } //.catalog-content__result

    &__sorting {
        margin-right: 30px;
        margin-left: auto;

        @include xs-block() {
            margin-right: 15px;
            margin-left: 0;
        }

        .bb-select {
            &::before {
                right: 10px;
            }
        } //.catalog-content__sorting .bb-select

        .selectize-control.single .selectize-input {
            border-color: $color-border--lighten;
        } //.catalog-content__sorting .selectize-control.single .selectize-input

        .selectize-dropdown.single {
            border-color: $color-border--lighten;
        } //.catalog-content__sorting .selectize-dropdown.single
    } //.catalog-content__sorting

    &__view {
        @include xs-block() {
            margin-left: auto;
        }
    } //.catalog-content__view

    &__map {
        position: relative;
        display: none;

        #catalog-map {
            width: 100%;
            height: 575px;
            margin-bottom: 30px;

            @include xs-block() {
                height: 425px;
            }

            @include i7-block() {
                height: 375px;
            }

            @include max(i5) {
                height: 320px;
            }
        }
    } //.catalog-content__map

    &__empty {
        width: 100%;
        padding: 30px 0;
    } //.catalog-content__empty

    &.is-loading {
        .loader__wrap {
            display: block;
        } //.catalog-content.is-loading .loader__wrap
    } //.catalog-content.is-loading
} //.catalog-content

.empty-search {
    justify-content: center;
    text-align: center;
    @include flexCenter;

    &__title {
        font-size: 20px;
        font-weight: 600;
    } //.empty-search__title

    &__text {
        display: block;
        margin-bottom: 30px;
        font-weight: 300;
    } //.empty-search__text

    &__icon {
        width: 50px;
        height: 50px;
        margin-bottom: 10px;
        fill: $color-success;
    }
} //.empty-search

.metro-checked {
    &__list {
        margin-bottom: 10px;
    } //.metro-checked__list

    &__item {
        position: relative;
        margin-right: 3px;
        color: $gray--light;
        font-size: 13px;
        @extend %textLight;
    } //.metro-checked__item

    &__icon {
        position: relative;
        top: -5px;
        cursor: pointer;
        @include tr;
        @include xs-block() {
            font-size: 17px;
        }

        &:hover {
            color: $gray--darkest;
        }
    } //.metro-checked__icon
} //.metro-checked
