$card-indent: $gutter * 2;

.card-menu {
    display: flex;
    margin-right: 30px;

    @include max(sm) {
        width: 100%;
        margin-right: 0;
    }

    &__wrap {
        margin: auto auto 15px;
        transition: background-color 0.2s ease;
        background-color: #fff;

        @include border(bottom);

        @include max(sm) {
            margin-bottom: $card-indent--mobile;
        }

        @include max(xs) {
            border: 0;
            border-radius: 0 0 $border-radius $border-radius;
        }

        &.is-sticky {
            margin-bottom: 0;
            border-radius: 0;
            background-color: $gray--darkest;

            @include max(sm) {
                background-color: #fff;
                box-shadow: $bxs;
            }

            .card-menu {
                &__link {
                    color: #fff;

                    @include max(sm) {
                        color: initial;

                        &::before {
                            color: $gray--light;
                        }
                    }

                    &:hover,
                    &:focus,
                    &:active {
                        color: $color-accent;

                        @include max(xs) {
                            color: $color-text;
                        }
                    }

                    &_see-gallary {
                        display: inline-block;
                    }
                }
            }
        } //.card-menu__wrap.is-sticky
    } //.card-menu__wrap

    &__inner {
        max-width: 1200px;
        margin: 0 auto;
        @include flexCenter;
    } //.card-menu__inner

    &__list {
        @include flexCenter;

        @include max(sm) {
            width: 100%;
            text-align: center;
        }
    } //.card-menu__list

    &__item {
        position: relative;
        height: 65px;
        overflow: hidden;
        line-height: 65px;

        @include max(sm) {
            width: 100%;
            height: 50px;
            line-height: 50px;
        }

        &_about {
            @include max(sm) {
                display: none;
            }
        } //.card-menu__item_about

        &_see-gallary {
            @include max-screen(1024) {
                margin-left: 90px;
            }

            @include max(sm) {
                margin-left: 0;

                &::before {
                    display: none;
                }
            }

            &::after {
                display: none !important;
            }
        } //.card-menu__item_see-gallary

        &.is-active {
            .card-menu__link {
                font-weight: 500;

                &::after {
                    display: block;
                }
            } //.card-menu__item.is-active .card-menu__link
        } //.card-menu__item.is-active
    } //.card-menu__item

    &__link {
        position: relative;
        display: inline-block;
        padding: 0 30px;
        color: $color-text;
        font-size: 15px;
        font-weight: 300;
        line-height: 65px;
        @include tr;

        @include max(sm) {
            width: 100%;
            line-height: 50px;
        }

        @include max(xs) {
            width: 100%;
            margin: auto;
            padding: 0;
            text-align: center;
        }

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            display: none;
            width: 100%;
            height: 3px;
            background-color: $color-accent;

            @include max(sm) {
                height: 2px;
            }
        }

        &:hover,
        &:focus {
            color: $color-accent;

            @include max(xs) {
                color: $color-text;
            }
        }
    } //.card-menu__link
} //.card-menu

.product-card {
    margin-bottom: $card-indent;

    @include max(md) {
        margin-bottom: $card-indent/2;
    }

    &__top-line {
        margin-bottom: $gutter;
        padding-top: 20px;
        @include flexCenter;

        @include max(xs) {
            margin: $gutter/2 0;
            padding-top: 0;
        }
    } //.card__top-line

    &__wrap {
        display: flex;
        margin-bottom: $card-indent * 2;

        @include max(xs) {
            flex-wrap: wrap;
            margin-bottom: $card-indent * 1.5;
        }
    } //.card__wrap

    &__image {
        position: relative;
        width: 40%;
        margin-right: $gutter * 3;

        @include max(sm) {
            width: calc(50% - 40px);
            margin-right: $gutter * 2;
        }

        @include max(xs) {
            width: 100%;
            margin-right: 0;
            margin-bottom: $card-indent/2;
        }
    } //.card__image

    &__badge {
        position: absolute;
        top: 16px;
        right: -16px;
        z-index: 2;
        @include max(xs) {
            top: auto;
            right: auto;
            bottom: -4px;
            left: 50%;
            flex-direction: row;
            @include tX(-50%);

            .badge {
                & + .badge {
                    margin-top: 0;
                    margin-left: 10px;
                }
            }
        }
    } //.card__badge

    &__slider {
        .slick-slide {
            margin: 0;
        }

        .slick-dots {
            bottom: -20px;

            li {
                button {
                    width: 6px;
                    height: 6px;
                    margin: 0 2px;
                    padding: 0;
                    border-radius: 50%;
                    background-color: lighten($gray, 15%);

                    &::before {
                        display: none;
                    }

                    &:hover {
                        width: 8px;
                        height: 8px;
                        background: $color-main;
                    }
                }

                &.slick-active button {
                    width: 8px;
                    height: 8px;
                    transition: initial;
                    background: $color-main;
                }
            }
        }

        &--nav {
            @include max(xs) {
                display: none;
            }

            .slick-slide {
                position: relative;
                margin: 0 3px;
                outline: none;
                cursor: pointer;

                &::after {
                    content: '';
                    display: none;
                    width: 100%;
                    height: 2px;
                    background-color: $color-main;
                }

                &.slick-current {
                    &::after {
                        display: block;
                    }
                }
            }
        }
    } //.card__slider

    &__desc {
        width: 60%;

        @include max(sm) {
            width: 50%;
        }

        @include max(xs) {
            width: 100%;
        }
    } //.card__desc

    &__title {
        display: block;
        margin: $gutter * 1.5 0;
        font-size: 30px;

        @include max(sm) {
            font-size: 28px;
        }

        @include max(xs) {
            margin: $gutter/2 0;
            font-size: 23px;
            text-align: left;
        }
    } //.card__title

    &__item-select {
        margin: 10px 0;

        @include max(xs) {
            max-width: 100%;
            margin: 0 0 10px;
        }
    } //.card__item-select

    &__price {
        font-size: 23px;

        span {
            font-size: 15px;
            font-weight: 300;
        }

        small {
            margin-left: 5px;
            color: $color-danger;
            font-size: 15px;
        }
    } //.card__price

    &__info-block {
        margin-top: 10px;
        margin-left: 10px;

        @include max(xs) {
            position: static;
            margin-top: 7px;

            .info-block__dropdown {
                top: 100%;
                right: 0;
            }
        }
    } //.card__info-block

    &__btn {
        position: relative;
        font-size: 16px;

        &--back {
            margin-bottom: 0;
            margin-left: auto;
            padding-left: 15px;
            color: $color-text;
            font-size: 14px;
            font-weight: 300;

            @include max(xs) {
                display: none;
            }

            &::before,
            &::after {
                content: '';
                position: absolute;
                top: 50%;
            }

            &::before {
                left: 0;
                width: 10px;
                height: 8px;
                margin: -4px 0 0;
                border-left: 0;
                border-radius: 0 5px 5px 0;
                background-clip: padding-box;
                @include border(all);
            }

            &::after {
                left: 1px;
                width: 5px;
                height: 5px;
                margin: -6px 0 0;
                transform: rotate(45deg);

                @include border(left);
                @include border(bottom);
            }

            &.button--accent {
                display: inline-block;
                margin: 15px auto;
                color: $color-main;

                @include max(xs) {
                    margin: $card-indent/2 auto 0;
                }

                &::before,
                &::after {
                    margin-left: 15px;
                    border-color: $color-main;
                    @include tr;
                }

                &:hover {
                    &::before,
                    &::after {
                        border-color: #fff;
                    }
                }
            }
        } //.card__btn--back

        &--cart {
            padding-right: 60px;
            padding-left: 20px;

            &:hover {
                span {
                    &:first-child {
                        &::before {
                            left: 12px;
                        }

                        &::after {
                            right: -100px;
                        }
                    }
                }
            }

            span {
                position: absolute;
                top: 0;
                right: 0;
                display: block;
                width: 45px;
                height: 45px;
                overflow: hidden;
                border-radius: 0 $border-radius $border-radius 0;
                background-color: $gray--darken;

                &::after,
                &::before {
                    color: #fff;
                    font-family: fal;
                    font-size: 18px;
                    @include centre('y');
                    @include tr();
                }

                &:first-child {
                    &::before {
                        content: '\f218';
                        left: -100px;
                    }

                    &::after {
                        content: '\f07a';
                        right: 13px;
                    }
                }

                &:last-child {
                    display: none;

                    &::after {
                        content: '\f2ed';
                        right: 14px;
                    }
                }
            }

            &.is-checked {
                background-color: $color-accent;
                color: #fff;

                span {
                    &:first-child {
                        display: none;
                    }

                    &:last-child {
                        display: block;
                    }
                }
            }
        } //.card__btn--cart

        &--fav {
            width: 45px;
            height: 45px;
            border-radius: 50%;

            @include border(left);

            &::before {
                font-size: 21px;
            }
        } //.card__btn--fav
    } //.card__btn

    &__back {
        position: relative;
        display: block;
        width: 100%;
        padding: 5px 0;

        font-size: 16px;
        text-align: center;
        text-transform: uppercase;
        @include min-screen(481) {
            display: none;
        }

        &::before {
            content: '\f104';
            left: 0;
            font-family: far;
            font-size: 20px;
            @include centre('y');
        }
    } //.card__back

    &__order-info {
        width: 100%;
        margin-top: 5px;
        font-size: 13px;

        span {
        }
    } //.card__order-info

    &__info {
        max-width: 375px;
        @include max(xs) {
            max-width: 100%;
        }

        .tab {
            &__titles {
                display: flex;
            }
        }
    } //.card__info

    &__related,
    &__colors {
        margin-bottom: $card-indent;

        @include max(xs) {
            margin-bottom: $card-indent/1.5;
        }
    }

    &__related {
        .tab {
            &__titles {
                padding-bottom: 1px;
                overflow-x: auto;

                @include max(xs) {
                    width: 100%;
                }
            }

            &__title {
                padding-bottom: 15px;

                &:hover {
                    .tab__text {
                        color: $color-main;
                    }
                }

                &.ui-tabs-active {
                    .tab__text {
                        color: $color-main;
                    }
                }
            }

            &__contentes {
                padding: 35px;
            }

            &__link {
                display: inline-flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
        }

        .bz-slider {
            &__arrow {
                top: 50px;
                height: 27px;
                line-height: 1;

                @include max(md) {
                    top: 58px;
                }

                @include max(sm) {
                    top: 47px;
                }

                @include max(xs) {
                    top: 36px;
                }

                @include max(xs) {
                    top: 51px;
                }

                @include max(i5) {
                    top: 37px;
                }

                &--prev {
                    left: -30px;
                }

                &--next {
                    right: -30px;
                }
            }
        }
    } //.card__related

    &__colors {
        @include max(xs) {
            margin-bottom: 0;
        }
    } //.card__colors

    &__selects {
        display: flex;
    }

    .button-box {
        flex-wrap: wrap;
        margin-bottom: $gutter * 1.5;
        @include flexCenter;

        .button--yellow {
            @include max(xs) {
                width: 85%;
            }
            @include max(xs) {
                width: 83%;
            }
            @include max(i5) {
                width: 80%;
            }
        }

        div {
            width: 45px;
            height: 45px;
            margin-left: 30px;

            @include max(xs) {
                margin-left: 15px;
            }
        }
    }

    .price {
        &__wrap {
            @include max(xs) {
                position: relative;
            }
        }
    }
} //.card

.product-card-item {
    flex-wrap: wrap;

    @include xs-block() {
        flex-direction: column;
        margin-top: 10px;
    }

    &__header {
        position: relative;
    } //.product-card-item__header

    &__badge-box {
        // position: absolute;
        // top: 0;
        // right: 0;
    } //.product-card-item__badge-box

    &__box {
        margin-bottom: 30px;
    } //.product-card-item__box

    &__changer {
        margin-bottom: 15px;

        .bb-checkbox--btn {
            margin-right: 10px;
        } //.product-card-item__changer .bb-checkbox--btn
    } //.product-card-item__changer

    &__image {
        position: relative;
        width: 100%;
        // max-width: 425px;
        overflow: hidden;
        border-radius: $border-radius;

        @include xs-block() {
            width: 100%;
            margin: 0;
        }

        &:hover {
            .slick-arrow {
                @include min(sm) {
                    visibility: visible;
                    opacity: 0.7;
                }
            }
        }

        .btn-icon {
            position: absolute;
            top: 10px;
            right: 15px;
            z-index: 2;

            @include xs-block() {
                top: 85%;
            }
        }

        img {
            width: 100%;
            height: auto;
        }

        .slick-slide {
            margin: 0 2px;
        }

        .slick-arrow {
            visibility: hidden;
            opacity: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            border: 0;
            border-radius: 50%;
            background-color: #ececec;
            text-align: center;
            @include tr;

            &::after {
                display: none;
            }

            &::before {
                color: $gray--light;
            }

            &:hover {
                opacity: 0.9;
                background-color: #ececec;
            }

            &.slick-prev {
                left: 15px;
            }

            &.slick-next {
                right: 15px;
            }
        }
    } //.product-card-item__image

    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        margin-bottom: $section-indent;

        @include xs-block() {
            margin-bottom: 30px;
        }
    } //.product-card-item__inner

    &__img {
        position: relative;
        flex-shrink: 0;
        width: 50%;
        // width: 100%;
        // max-width: 425px;
        margin-right: 50px;

        @include sm-block() {
            width: 50%;
            margin-right: 25px;
        }

        @include xs-block() {
            width: 100%;
            margin: 0;
        }

        @include landscape() {
            width: 100%;
            max-width: 270px;
            margin-right: 30px;
        }

        // .bb-slider__preview {
        //     @include xs-block() {
        //         margin-right: 30px;
        //     }
        // }
    } //.product-card-item__img

    &__fav {
        position: absolute;
        right: 8px;
        bottom: 65px;
    } //.product-card-item__fav

    &__prop {
        display: flex;
        flex: 1;
        flex-direction: column;
        width: 100%;
        max-width: 35%;
        padding-top: 6px;
        @include sm-block() {
            width: 100%;
            max-width: none;
        }

        @include xs-block() {
            width: 100%;
            margin-top: 20px;
        }
    } //.product-card-item__prop

    &__subtitle {
        color: $color-text--darkest;
        font-size: 20px;
        font-weight: 500;

        &.bb-accordeon__title {
            padding: 18px 0;

            &::before {
                color: $gray--darken;
            } //.product-card-item__subtitle.bb-accordeon__title::before

            .icon-angle--bold {
                right: 0;
            } //.product-card-item__subtitle.bb-accordeon__title.icon-angle--bold
        } //.product-card-item__subtitle.bb-accordeon__title
    } //.product-card-item__subtitle

    &__name {
        color: $gray--darken;
        font-size: 26px;
        font-weight: 500;
        line-height: 1.4;

        @include xs-block() {
            font-size: 18px;
        }
    } //.product-card-item__name

    &__cost {
        margin: 15px 0;
        color: $gray--darken;
        font-size: 24px;
        font-weight: 500;

        @include xs-block() {
            font-size: 20px;
        }

        small {
            font-size: 16px;
        }
    } //.product-card-item__cost

    &__desc {
        width: 50%;
        // max-width: 425px;
        // text-align: justify;
        justify-items: flex-start;

        @include xs-block() {
            width: 100%;
        }
    } //.product-card-item__desc

    &__btns {
        display: flex;
        align-items: flex-start;

        .btn-box {
            flex-grow: 1;
            margin-right: 20px;
        } //.product-card-item__btns .btn-box

        .btn-icon {
            flex-shrink: 0;
        } //.product-card-item__btns .btn-icon
    } //.product-card-item__btns

    &__btn {
        position: relative;
        width: 100%;
        padding-right: 50px !important;
        @include text-overflow;

        &:not(.is-checked) {
            .product-card-item__btn-icon {
                pointer-events: none;
            }
        } //.product-card-item__btn:not(.is-checked)

        &.is-checked {
            background-color: $color-success;

            .in-cart {
                display: none;
            }

            .delete-from-cart {
                display: flex;
            }
        } //.product-card-item__btn.is-checked
    } //.product-card-item__btn

    &__btn-info {
        bottom: 0;
        color: $color-text;
        @include centre('x');
    } //.product-card-item__btn-info

    &__btn-icon {
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        align-items: center;
        justify-content: flex-start;
        justify-content: center;
        width: 42px;
        max-width: 42px;
        min-height: 100%;
        overflow: hidden;
        background-color: $gray;

        .icon {
            flex-shrink: 0;
            width: 20px;
            height: 20px;
            margin: auto;
            transition: 0.2s;
            fill: #fff;
        } //.product-card-item__btn-icon .icon

        .in-cart {
            display: flex;
        } //.product-card-item__btn-icon .in-cart

        .delete-from-cart {
            display: none;
        } //.product-card-item__btn-icon .delete-from-cart
    } //.product-card-item__btn-icon

    &__text {
        font-size: 15px;
        text-align: left;

        p {
            margin-bottom: 10px;
        }
    } //.product-card-item__text

    &__quantity {
        display: flex;
        flex-direction: column;
        width: 100%;
        @include xs-block() {
            width: 100%;
            max-width: none;
        }
    } //.product-card-item__quantity

    &__info {
        display: inline-block;
        align-self: center;
        margin-bottom: 10px;
        font-size: 14px;
        text-align: center;

        @include xs-block() {
            width: 100%;
        }

        span + span {
            margin-left: 5px;
        }
    } //.product-card-item__info

    .box {
        .box__item {
            display: flex;
            align-items: flex-start;
            padding-top: 10px;

            &:not(:first-child) {
                padding-top: 20px;
            }

            .icon {
                flex-shrink: 0;
                width: 25px;
                height: 25px;
                fill: $gray;
            } //.product-card-item .box__item .icon

            .bb-title {
                width: 100%;
                margin-bottom: 0;
                padding-left: 15px;
                line-height: 1.2;
                text-align: left;

                &__title {
                    font-size: 16px;
                } //.product-card-item .box__item .bb-title__title

                &__sub {
                    font-size: 14px;

                    &.btn-link {
                        font-weight: 400;
                    }
                } //.product-card-item .box__item .bb-title__sub
            } //.product-card-item .box__item .bb-title

            .bb-input {
                display: block;

                input {
                    display: block;
                    width: 100%;
                    max-width: auto;
                } //.product-card-item .box__item .bb-input input
            } //.product-card-item .box__item .bb-input
        } //.product-card-item .box__item
    } //.product-card-item .box
} //.product-card-item

.move-block {
    &__footer {
        .product-card-item__btn {
            margin-top: 0;

            font-size: 16px;
            text-transform: initial;

            @include xs-block() {
                margin-top: 0;
                font-size: 17px;
            }
        } //.move-block__footer .product-card-item__btn
    } //.move-block__footer
} //.move-block
