.catalog {
    &__changer {
        margin-bottom: 10px;

        @include sm-block() {
            margin-bottom: 25px;
        }
    } //.catalog__changer
} //.catalog

.catalog-filter {
    &__inner {
        @include box;

        @include min(xs) {
            box-shadow: none;
            @include border(all);
        }

        @include sm-block() {
            display: flex;
            flex-direction: column;
            height: 100%;
            border: 0;
        }
    } //.catalog-filter__inner
} //.catalog-filter
