$container-width: 880px;

.order {
    background-color: $color-bg--lightest;

    @include sm-block() {
        padding-top: 0;
    }

    @include xs-block() {
        background-color: $color-bg--lighten;
    }

    &:not(.order--info) {
        padding-bottom: 30px;

        @include sm-block() {
            padding-bottom: 0;
        }
    }

    &__top-line {
        position: relative;
        margin-bottom: 15px;

        border: 1px solid $color-bg--lighten;
        background-color: #fff;

        @include blockHeight;
        @include flexCenter;

        @include sm-block() {
            position: fixed;
            top: 0;
            z-index: z(fixed);
            width: 100%;
            height: 50px;
            margin-bottom: 0;
            background-color: #fff;
            color: $gray;
            line-height: 50px;
        }

        // & + .order__inner {
        //     margin-top: 50px;
        //     padding-top: 10px;
        // }//.order__top-line + .order__inner //Не раскоментировать

        .container {
            position: relative;
        } //.order__top-line .container

        .btn-link {
            left: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: $base-header-size;
            height: $base-header-size;
            color: $color-icon;

            @include baseSize;
            @include centre('y');

            @include sm-block() {
                left: 0;
                width: $base-header-size--small;
                height: $base-header-size--small;
            }

            @include xs-block() {
                left: -5px;
            }

            &:hover {
                color: $color-accent;
                text-decoration: none;

                .icon {
                    fill: $color-accent;
                }
            }

            .icon {
                margin: auto;
                fill: $color-icon;
            } //.order__top-line .btn-link .icon

            i {
                margin-top: 0;
                font-size: 20px;
            } //.order__top-line .btn-link i

            span {
                @include xs-block() {
                    display: none;
                }
            } //.order__top-line .btn-link span
        } //.order__top-line .btn-link

        &--narrow {
            .container {
                max-width: $container-width;
            }
        } //.order__top-line--narrow
    } //.order__top-line

    &__inner {
        position: relative;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        @extend .container;

        @include min(md) {
            justify-content: space-between;
        }

        @include sm-block() {
            flex: 1;
            flex-direction: column;
            margin-top: 60px;
            padding: 0;
            box-shadow: none;
        }

        @include xs-block() {
            padding: 0;
        }

        &--form {
            max-width: $container-width;
            margin-top: 30px;

            @include sm-block() {
                margin-top: 30px;
                margin-bottom: 0;
                padding: 50px 0 0;
                border-top: 0;
            }
        } //.order__inner--form
    } //.order__inner

    &__items {
        overflow: hidden;
        border: 1px solid $color-border-very-light;
        border-radius: $border-radius * 3;
        background-color: #fff;

        @include xs-block() {
            border: 0;
            border-radius: 0;
            background-color: transparent;
        }

        .market-product-item {
            margin-right: 15px;
            margin-left: 15px;
            border-color: $color-border-very-light;
            cursor: default;

            @include xs-block() {
                margin: 0 0 10px;
                border: 0;
            }
        } //.order__items .market-product-item
    } //.order__items

    &__left {
        width: calc(100% - 360px - 30px);

        @include flexColumn;

        @include sm-block() {
            width: 100%;
            border: 0;
        }

        @include xs-block() {
            background-color: transparent;
        }

        &--offset {
            padding: 0 15px;

            @include xs-block() {
                padding: 0;
            }
        } //.order__left--offset
    } //.order__left

    &__right {
        width: 360px;
        margin-left: 30px;

        @include sm-block() {
            width: auto;
            margin: 30px 15px 20px;
        }

        @include xs-block() {
            margin-right: 0;
            margin-left: 0;
        }
    } //.order__right

    &__wrap {
        position: relative;
        display: flex;
        justify-content: space-between;
        @extend .container;
    } //.order__wrap

    &__title {
        flex: 1;
        justify-content: center;
        color: $gray--darkest;

        font-size: 18px;
        @include flexCenter;

        @include min(md) {
            font-size: 20px;
        }
    } //.order__title

    &__empty {
        margin: auto;
        padding: 50px 0;

        @include xs-block() {
            flex: 1;
            width: 100%;
            margin: initial;
            background-color: #fff;
        }
    } //.order__empty

    &--info {
        padding-top: 0;

        @include min(md) {
            padding-top: 30px;
        }
    } //.order--info

    .btn-icon {
        padding: 0;
        background-color: transparent;

        &:hover {
            cursor: pointer;
        } //.icon:hover

        &--delete {
            position: absolute;
            top: 50%;
            left: 70px;
            width: 20px;
            height: 20px;
            overflow: hidden;
            transform: translateY(-50%);
            @include tr;

            .fa-minus-circle {
                color: $color-accent;
                font-size: 20px;
            }
        } //.icon--delete
    } //.order .icon

    .modal {
        &-dialog {
            display: flex;
            justify-content: center;
        } //.order .modal-dialog

        &-header {
            align-items: center;

            .btn {
                margin-left: auto;
                font-weight: 300;
            }
        }

        &-content {
            @include min(md) {
                max-width: 330px;
            }
        } //.order .modal-content

        &-title {
            font-weight: 500;
        } //.order .modal-title

        &-body {
            &__scroll-wrap {
                max-height: 420px;
                overflow-y: auto;
                @include bb-scroll--thin;
            } //.order .modal-body__scroll-wrap
        }

        &-footer {
            display: flex;
            flex-shrink: 0;
            justify-content: center;
            margin-top: auto;
            background-color: transparent;

            .btn {
                padding: 3px 35px;
                font-size: 15px;
            }
        } //.order .modal-footer

        .btn-link {
            padding: 0;
        }
    } //.order .modal
} //.order

.box-cart {
    display: flex;
    flex-direction: column;
    counter-increment: section;

    @include xs-block() {
        margin-top: 10px; // НЕ ИЗМЕНЯТЬ, НУЖНО ДЛЯ ПРОДАКШЕНА
    }

    &__header {
        // margin-bottom: 15px;
        @include flexCenter;

        @include xs-block() {
            padding: 0 10px;
        }
    } //.box-cart__header

    &__counter {
        width: 25px;
        height: 25px;
        margin-right: 10px;
        border-radius: 50%;
        background-color: $color-bg--dark;
        color: #fff;
        font-size: 15px;
        font-weight: 500;
        line-height: 25px;
        text-align: center;

        &::before {
            content: counter(section);
        } //.box-cart__counter::before
    } //.box-cart__counter

    &__title {
        position: relative;
        font-size: 21px;
        font-weight: 500;
        letter-spacing: 1px;

        &--small {
            display: inline-block;
            font-size: 16px;
            letter-spacing: 0;
        } //.box-cart__title--small
    } //.box-cart__title

    &__inner {
        padding: 20px;
        border-radius: $border-radius * 3;
        background-color: #fff;

        @include border(all, $color-border-very-light);

        @include xs-block() {
            padding: 15px;
            border: 0;
            border-radius: 0;
        }

        &--transparent {
            border-color: transparent;
            background-color: transparent;
        } //.box-cart__inner--transparent

        &--flex {
            display: flex;
            align-items: center;
            justify-content: space-between;
        } //.box-cart__inner--flex

        &--no-offset {
            padding: 0;
        } //.box-cart__inner--no-offset

        input {
            width: 100%;
        } //.box-cart__inner input

        .box-cart__title {
            margin-bottom: 10px;
        } //.box-cart__inner .box-cart__title
    } //.box-cart__inner

    &__row {
        @include row-flex;
    } //.box-cart__row

    &__item {
        @include col;
        @include size(12);
        @include size-sm(6);
        @include size-xs(12);
        // margin-bottom: 20px;

        & + .box-cart__item {
            margin-top: 20px;

            @include xs-block() {
                margin-top: 15px;
            }
        }

        &--small {
            @include size(6);
            @include size-sm(3);
            @include size-xs(6);
        } //.box-cart__item--small
    } //.box-cart__item

    &__radio-group {
        display: inline-flex;

        &--native {
            padding-left: 15px;

            .bb-checkbox + .bb-checkbox {
                margin-left: 10px;
            }
        } //.box-cart__radio-group--native
    } //.box-cart__radio-group

    &__map {
        width: 100%;
        height: 400px;

        & > *[id] {
            width: 100%;
            min-height: 100%;
        }
    } //.box-cart__map

    & + .box-cart {
        margin-top: 40px;

        @include xs-block() {
            margin-top: 20px;
        }
    }

    & > * + * {
        margin-top: 15px;
    }

    .bb-input {
        width: 100%;
    } //.box-cart .bb-input

    .button-box {
        @include xs-block() {
            padding: 15px;
            background-color: #fff;
        }

        & + .box-cart__inner {
            margin-top: 30px;
        }
    } //.box-cart .button-box
} //.box-cart

.info-block-btn {
    padding: 15px;
    background-color: #fff;

    @include border(top);
    @include tr(transform, 0.4s);

    @include xs-block() {
        padding: 10px;
    }

    &__inner {
        justify-content: space-between;
        width: 100%;
        @include flexCenter;
    } //.info-block-btn__inner

    &__text {
        flex: 1;
    } //.info-block-btn__text

    &__btn {
        flex-shrink: 0;
        margin-left: 10px;
    } //.info-block-btn__btn

    &--fixed-bottom {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: z(fixed);
    } //.info-block-btn--fixed-bottom

    &.is-hide {
        @include tY(150%);
    } //.info-block-btn.is-hide
} //.info-block-btn

.cart-info {
    margin-left: auto;
    font-weight: 500;

    @include xs-block() {
        width: 100%;
        margin-bottom: 0;
        padding-right: 15px;
        padding-left: 15px;
        text-align: left;
    }

    &__inner {
        padding: 25px;
        border-radius: $border-radius * 3;
        background-color: #fff;
        @include border(all, $color-border-very-light);

        @include xs-block() {
            margin: 0 -15px;
            padding: 15px;
            border: 0;
            border-radius: 0;
        }
    } //.cart-info__inner

    &__btn {
        width: 100%;
        margin-top: 20px;
        font-size: 16px;
    } //.cart-info__btn

    &__info {
        display: block;
        margin-top: 15px;
        color: $color-text--light;

        font-size: 15px;
        line-height: 1.4;
    } //.cart-info__info

    &__item {
        justify-content: space-between;
        color: $color-text--dark;
        font-size: 17px;
        font-weight: 500;
        @include flexCenter;

        &_left {
        } //.cart-info__item_left

        &_right {
        } //.cart-info__item_right

        &_top {
        } //.cart-info__item_top

        &_bottom {
            color: $color-text--light;

            font-size: 14px;
            font-weight: 400;

            span {
                &:not(:first-child) {
                    margin-left: 3px;

                    &::before {
                        content: '\2022';
                        margin-right: 6px;
                    }
                }
            }

            a {
                color: $color-info--light;

                &:hover {
                    color: $color-accent;
                }
            }
        } //.cart-info__item_bottom

        & + .cart-info__item {
            margin-top: 7px;

            &--itog {
                margin-top: 15px;
            }
        } //.cart-info__item + .cart-info__item

        &--itog {
            padding-top: 15px;
            border-top: 2px solid $color-border--dark;
            font-size: 22px;
            font-weight: 500;

            .cart-info__sum {
                margin-right: 3px;
                font-size: 22px;
            } //.sum-cart__item--itog span
        } //.cart-info__item--itog

        &--sale {
            .cart-info {
                &__sum {
                    color: $color-danger--light;
                } //.cart-info__item--sale .cart-info__sum

                &__currency {
                    color: $color-danger--light;
                } //.cart-info__item--sale .cart-info__currency
            } //.cart-info__item--sale .cart-info
        } //.cart-info__item--sale

        &--counter {
            .cart-info {
                &__sum {
                    margin-right: 3.5px;
                } //.cart-info__item--counter .cart-info__sum

                &__currency {
                    padding-right: 0;

                    font-size: 12px;
                    font-weight: 400;
                } //.cart-info__item--counter .cart-info__currency
            } //.cart-info__item--counter .cart-info
        } //.cart-info__item--counter
    } //.cart-info__item

    &__sum {
        margin-left: auto;
        font-size: 18px;
        font-weight: 500;
    } //.cart-info__sum

    &__currency {
        color: $color-text--light;
        font-weight: 500;

        @include xs-block() {
            margin-left: 5px;
        }
    } //.cart-info__currency
} //.cart-info

.cart-form {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    padding: 30px;

    @include xs-block() {
        padding: 0 15px 15px;
        background-color: #fff;

        @include border(top);
    }

    &__wrap {
        display: flex;
        justify-content: center;
    } //.cart-form__wrap

    &__title {
        width: 100%;
        margin-bottom: 15px;
        font-size: 20px;
        font-weight: 500;
        text-align: left;

        @include xs-block() {
            margin-top: 20px;
            font-size: 24px;
        }
    } //.cart-form__title

    &__item {
        width: 49%;
        margin-bottom: 20px;

        @include xs-block() {
            width: 100%;
        }

        .bb-input {
            &__title {
                @include text-overflow;
            }
        } //.cart-form__item .bb-input

        &--small {
            width: 49%;
            @include xs-block() {
                width: 100%;
            }
        } //.cart-form__item--small

        &--check {
            display: flex;
            align-items: flex-start;
            width: 100%;
            margin-top: 10px;
            margin-bottom: 30px;
            text-align: left;

            .bb-checkbox {
                padding-left: 0;

                &__box {
                    background-color: #fff;
                }
            } //.cart-form__item--check .bb-checkbox

            .policy {
                margin-top: 0;
                text-align: unset;
            } //.cart-form__item--check .policy
        } //.cart-form__item--check
    } //.cart-form__item

    &__inner {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 15px;

        @include xs-block() {
            margin-bottom: 0;
        }
    } //.cart-form__inner

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        @include xs-block() {
            .btn {
                z-index: 0;
                width: 100%;
            }
        }

        .btn {
            z-index: 0;
        }
    } //.cart-form__btn

    &__continue {
        width: 100%;
    } //.cart-form__continue

    &__title-text {
        font-size: 14px;
        font-weight: 500;
    } //.cart-form__title-text

    .bb-input__title {
        @include text-overflow;
    } //.cart-form .bb-input__title

    &--full {
        margin-bottom: 100px;
        @include xs-block() {
            margin-bottom: 30px;
            padding: 0;
            padding-bottom: 15px;
        }

        .cart-form__btn {
            width: 100%;

            .btn {
                width: 100%;
            }
        } //.cart-form--full .cart-form__btn

        .cart-form__item {
            width: 100%;
        } //.cart-form--full .cart-form__item
    } //.cart-form--full

    &.is-confirm {
        .form-success--confirm {
            z-index: z(success);
            @include tY(0);
        }

        .fancybox-close-small {
            z-index: z(successBtnClose);
            display: block;
        }

        .btn {
            box-shadow: none;
        }
    } //.form.is-confirm
} //.cart-form

.master-picker {
    width: 100%;
    padding: 15px 17px;
    border-bottom: 1px solid $color-border--lightest;
    @include flexCenter;

    @include min(md) {
        margin-bottom: 30px;
        border: 1px solid $color-border--lightest;
    }

    &:hover {
        cursor: default;
    }

    &__item {
        position: relative;
        height: 70px;
        padding: 10px 15px;
        @include flexCenter;

        & + & {
            border-top: 1px solid $color-border--lightest;
        } //.modal-box__item + .modal-box__item

        .user {
            margin-right: auto;
            @include flexCenter;

            &__img {
                width: 40px;
                height: 40px;
            } //.user__img

            &__avatar {
                margin-right: 13px;
                margin-bottom: 0;
            } //.user__avatar
        } //.user

        .bb-checkbox {
            flex: 1;

            &__box {
                margin: 0;
            }
        } //.master-picker__item .bb-checkbox__box
    } //.master-picker__item

    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;

        &_top {
            font-size: 15px;
            line-height: 1.4;
        } //.master-picker__title_top

        &_bottom {
            color: $color-text--light;
            font-size: 14px;
            font-style: italic;
        } //.master-picker__title_bottom
    } //.master-picker__title

    &__btn {
        margin-left: auto;
        padding: 10px 0 10px 10px;
        font-size: 15px;
        font-weight: 300;
    } //.master-picker__btn

    .user {
        margin-right: 20px;
        line-height: 1;

        &__avatar {
            margin-bottom: 0;
        } //.user__avatar

        &__img {
            width: 35px;
            height: 35px;
        } //.user__img
    } //.user
} //.master-picker

.confirm-msg {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px;
    border: 1px solid #d7eacf;
    background-color: #f4f9f2;
    font-size: 15px;

    @include min(md) {
        width: 100%;
        margin: 0;
    }

    &:hover {
        cursor: default;
    }

    &__icon {
        margin: 0 20px 0 10px;

        i {
            color: #6fb557;
            font-size: 40px;
        }
    } //.confirm-msg__icon

    &__title {
        font-weight: 500;

        @include min(md) {
            margin-bottom: 5px;
        }
    } //.confirm-msg__title
} //.confirm-msg

.user-info {
    width: 100%;
    @extend %boxLight;

    @include max(md) {
        border: 0;
        box-shadow: none;
    }

    &__inner {
        z-index: 10;
        width: 100%;
        transition: margin-left 0.5s;
        background-color: #fff;
    } //.user-info__inner

    &__wrap-scroll {
        max-height: 400px;
        overflow: auto;
    } //.user-info__wrap-scroll

    &__top {
        position: relative;
        padding: 20px;
        overflow: hidden;
        border-bottom: 1px solid $color-border--lightest;
        @include tr;
        @include flexColumn;

        &.is-active {
            &:hover {
                cursor: default;
            }

            .user-info {
                &__date {
                    top: 50px;
                    visibility: visible;
                    opacity: 1;
                    @include tr;
                } //.user-info__date

                &__title {
                    color: $color-text--light;
                    font-size: 12px;
                    font-style: italic;
                    @include tr;
                } //.user-info__title

                &__header {
                    margin-bottom: 27px;
                } //.user-info__user
            } //.user-info
        } //.user-info__top.is-active

        .user-info__title {
            color: $color-text;
            font-size: 17px;
            font-weight: 500;
        } //.user-info__top .user-info__title
    } //.user-info__top

    &__bottom {
        position: absolute;
        right: -100%;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        width: 100%;
        border-top: 1px solid $color-border--lightest;
        background-color: $color-bg--light;
        @include tr;

        @include min(md) {
            position: relative;
            right: 0;
            padding: 10px;
            background-color: transparent;
        }

        .user-info__title {
            font-weight: 500;
            text-transform: uppercase;
        } //.user-info__title

        &.is-active {
            right: 0;
            @include tr;

            @include min(md) {
                padding: 10px;
            }
        }
    } //.user-info__bottom

    &__item {
        position: relative;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 12.5px 35px;

        @include min(md) {
            padding: 20px;
        }

        &:hover {
            cursor: default;
        }

        &.has-hover {
            &:hover {
                background-color: $color-bg--hover;
                cursor: default;

                .user-info__inner {
                    margin-left: 32px;
                    transition: margin-left 0.5s;
                    background-color: $color-bg--lightest;
                }

                .btn-icon {
                    left: 20px;
                }
            }
        } //.service-list__item.has-hover

        & + .user-info__item {
            border-top: 1px solid $color-border--lightest;
        }
    } //.user-info__item

    &__header {
        @include flexCenter;

        .user {
            @include flexCenter;

            &__img {
                width: 40px;
                height: 40px;
                margin-right: 15px;
            }

            &__title {
                color: $color-text;
                font-size: 16px;
                font-weight: 500;
            }
        } //.user-info__title
    } //.user-info__header

    &__details {
        display: flex;
        align-items: center;
        font-size: 14px;
    } //.user-info__details

    &__left {
        width: 70%;
        color: $color-text--light;
        font-size: 14px;
        font-style: italic;
        font-style: italic;
    } //.user-info__left

    &__right {
        margin-left: auto;
        color: $gray--dark;
        font-size: 16px;
        font-weight: 500;

        &--total {
            margin-left: auto;
            font-size: 18px;
            font-weight: 500;
        } //.user-info__right--price
    } //.user-info__right

    &__user {
        display: flex;
        align-items: center;

        .user {
            &__avatar {
                margin-right: 20px;
                margin-bottom: 0;
            } //.user__avatar

            &__img {
                width: 45px;
                height: 45px;
            } //.user__img
        }
    } //.user-info__user

    &__title {
        color: $color-accent;
        font-weight: 300;
    } //.user-info__title

    &__date {
        position: absolute;
        top: -50%;
        visibility: hidden;
        opacity: 0;
    } //.user-info__date

    &__note {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 11.5px 20px;

        @include min(md) {
            padding: 10px;
        }
    } //.user-info__desc

    &__btn {
        position: relative;
        display: none;
        justify-content: space-between;
        width: 100%;
        height: 50px;
        padding: 0 25px;
        border-radius: 0;
        letter-spacing: 0.5px;
        line-height: 51px;
        text-transform: uppercase;

        @include min(md) {
            top: -100px;
            z-index: -1;
            width: 100%;
            overflow: hidden;
            border-radius: $border-radius;
        }

        @include sm-block() {
            box-shadow: none;
        }

        @include xs-block() {
            padding: 0 25px;
        }

        &.is-active {
            top: 0;
            z-index: 1;
            display: flex;
            @include tr;
        }
    } //.user-info__btn

    &--confirmed {
        .user-info__title {
            color: $color-text;
        } //.user-info__title
    } //.user-info--confirmed

    &.is-fixed {
        position: fixed;
        top: 0;
        width: 384px;
    } //.user-info.is-fixed
} //.user-info

.info-box {
    width: 100%;
    margin-bottom: 30px;
    padding: 20px;
    @extend %boxLight;

    &:hover {
        cursor: default;
    }

    &__title {
        margin-bottom: 8px;
        color: $gray--darkest;

        font-size: 18px;
    } //.service-box__title

    &__desc {
        font-size: 15px;
        font-weight: 300;
    } //.info-box__desc
} //.info-box
