//noUiSlider

.noUi-horizontal {
    height: 2px;

    .noUi-handle {
        top: -3px;
        left: -5px;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $color-accent;
        @include max(xs) {
            width: 10px;
            height: 10px;
        }
    }
}

.noUi-base,
.noUi-handle {
    background-color: $gray--lighten;
}

.noUi-handle {
    position: relative;
    border: 0;
    border-radius: 0;
    outline: none;
    box-shadow: none;
    cursor: pointer;

    &::before {
        display: none;
    }

    &::after {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: rgba($color-accent, 0.5);
        @include centre;
        @include tr(0.5s);
        @include max(xs) {
            width: 28px;
            height: 28px;
        }
    }
}

.noUi-connect,
.noUi-target {
    border: 0;
    border-radius: 0;
    background: $color-accent;
    box-shadow: none;
}

.range {
    @include clr;

    &__slider {
        margin-top: 5px;
        margin-bottom: 15px;
    } //.range__slider

    &__from,
    &__to {
        display: inline-block;
        color: $color-text--light;
        font-size: 14px;
        vertical-align: middle;

        span {
            color: $gray--dark;
            font-size: 15px;
            font-weight: 500;
        }

        i {
            color: $gray--light;
            font-size: 13px;
        }
    } //.range__from

    &__from {
        float: left;
    }

    &__to {
        float: right;
    } //.range__to
} //.range
