.footer {
    // background-color: $color-bg--light;
    background-color: $color-bg--lighten;
    @include nosel;

    @include xs-block() {
        padding: 5px 0 0;
    }

    &__in {
        display: flex;
        justify-content: center;
        @include xs-block() {
            flex-direction: column;
        }
    } //.footer__in

    &__social {
        .icon-list {
            margin-right: 0;
            margin-left: 0;

            &__link {
                display: inline-block;
                margin-left: 15px;

                &:first-child {
                    margin-left: 0;
                } //.footer__social .icon-list__link:first-child
            } //.footer__social .icon-list__link
        }
    } //.footer__social

    &__top {
        padding: 30px 0;
        @include xs-block() {
            padding: 0;
        }

        .container {
            @include xs-block() {
                margin: 0;
                padding: 0;
            }
        } //.footer__top .container
    } //.footer__top

    &__bottom {
        padding: 10px 0 8px;
        background: #fff;

        @include xs-block() {
            padding: 10px 0;
        }

        h4 {
            margin-bottom: 0;
        }
    } //.footer__bottom

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include xs-block() {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        @include landscape() {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    } //.footer__inner

    &__social-icon {
        width: 30px;
        height: 30px;

        &:hover {
            fill: $gray--dark;
        }
    } //.footer__social-icon

    &__info {
        display: flex;
        font-size: 13px;
        text-align: center;
        @include xs-block() {
            flex-direction: column;
        }

        @include landscape() {
            flex-direction: column;
        }

        span {
            margin-left: 10px;
            color: $gray;
            font-weight: 300;
            font-weight: 300;
            @include xs-block() {
                margin-left: 0;
                font-size: 10px;
            }

            @include landscape() {
                margin-bottom: 10px;
                margin-left: 0;
                font-size: 10px;
            }
        }
    } //.footer__info

    .bb-accordeon {
        &__title {
            .icon-angle--bold {
                right: 0;
            } //.footer .bb-accordeon__title .icon-angle--bold
        } //.footer .bb-accordeon__title
    } //.footer .bb-accordeon
} //.footer

.footer-item {
    @include col;
    @include size(3);
    @include size-sm(6);
    @include size-xs(12);

    @include sm-block() {
        margin-bottom: $gutter;
    }

    @include xs-block() {
        width: 100% !important;
        margin: 0;
        border-top: 1px solid #d9d9d9;

        &:first-child {
            border: 0;
        }

        & + & {
            margin-top: 0;
        }
    }

    &__title {
        margin-bottom: 15px;
        color: $gray--darken;
        font-size: 15px;
        font-weight: 500;
        letter-spacing: 0.5px;

        @include xs-block() {
            margin-bottom: 0;
            padding: 18px 10px;
            font-size: 17px;
        }
    } //.footer-item__title

    &__content {
        @include xs-block() {
            margin: 0;
            padding-top: 5px;
            padding-bottom: 10px;
        }
    } //.footer-item__content

    &__list {
        @include flexColumn;
    } //.footer-item__list

    &__el {
        color: $color-text;
        font-size: 14px;
        letter-spacing: 0.5px;
        @include flexCenter;

        & + & {
            margin-top: 10px;
        }
    } //.footer-item__el

    &__link {
        color: $color-text;
        font-size: 12px;
        letter-spacing: 0.5px;

        @include xs-block() {
            font-size: 15px;
        }

        &:hover {
            color: $color-accent;
        }
    } //.footer-item__link

    &__icon {
        margin-right: 10px;
        font-size: 18px;

        &.fa-map-marker-alt {
            margin-left: 2px;
        }
    } //.footer-item__icon

    &.bb-accordeon__item {
        margin-top: 0;
    }
} //.footer-item

.copyright {
    display: block;
    width: 100%;
    font-size: 13px;

    @include xs-block() {
        text-align: center;
    }
} //.copyright
